.PushNotification {
  position: fixed;
  border-radius: 8px;
  background-color: var(--background_light);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -webkit-animation: PushNotification__anim 200ms ease-in-out forwards;
          animation: PushNotification__anim 200ms ease-in-out forwards;
  padding: 16px 6px 16px 16px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  top: 51px;
  margin-top: env(safe-area-inset-top);
  left: 8px;
  right: 8px;
  z-index: 1000;
}
.PushNotification.hasShadow {
  -webkit-box-shadow: 0 4px 24px 0 rgba(0, 0, 0, 0.16);
          box-shadow: 0 4px 24px 0 rgba(0, 0, 0, 0.16);
}
body.desktop .PushNotification {
  right: 248px;
}
@-webkit-keyframes PushNotification__anim {
  from {
    -webkit-transform: translateY(-115%);
            transform: translateY(-115%);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@keyframes PushNotification__anim {
  from {
    -webkit-transform: translateY(-115%);
            transform: translateY(-115%);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
.PushNotification__icon {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: no-repeat center / cover;
}
.PushNotification.like .PushNotification__icon {
  background-image: url('../containers/Main/asset/liked_24.svg');
}
.PushNotification.message .PushNotification__icon {
  background-image: url('../containers/ProfileView/asset/ic_message.svg');
}
.PushNotification.swipe_right .PushNotification__icon {
  background-image: url('../containers/Main/asset/liked_24.svg') !important;
}
.PushNotification__cont {
  margin-left: 12px;
  overflow: hidden;
  -ms-flex: 1 1;
      flex: 1 1;
}
.PushNotification__title {
  color: var(--text_primary);
  font-size: 16px;
  line-height: 20px;
  font-weight: 500;
}
.PushNotification__message {
  font-size: 14px;
  line-height: 18px;
  color: var(--text_secondary);
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  margin-top: 4px;
}
.PushNotification__close {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  width: 48px;
  height: 48px;
  background: url('../asset/close_16.svg') no-repeat center;
  background-size: 16px;
  cursor: pointer;
  margin-left: 12px;
}



