.InternalNotification {
  display: -ms-flexbox;
  display: flex;
  padding: 12px;
  background: var(--background_content);
  border-bottom: 1px solid var(--separator_common);
  margin: 16px 0 8px;
}
.InternalNotification__icon {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  width: 42px;
  height: 42px;
  background: no-repeat center / contain;
  margin-right: 12px;
  color: var(--text_primary);
}
.InternalNotification__icon svg {
  -webkit-transform: scale(2.625);
      -ms-transform: scale(2.625);
          transform: scale(2.625);
  -webkit-transform-origin: left top;
      -ms-transform-origin: left top;
          transform-origin: left top;
}
.InternalNotification__cont {
  -ms-flex: 1 1;
      flex: 1 1;
}
.InternalNotification__title {
  font-size: 18px;
  line-height: 21px;
  font-weight: bold;
  color: var(--text_primary);
}
.InternalNotification__text {
  font-size: 16px;
  line-height: 24px;
  color: var(--text_secondary);
  margin-top: 8px;
  margin-bottom: 12px;
}



